@font-face {
  font-family: 'regular-font';
  src: URL('/light.ttf') format('truetype');
}

@font-face {
  font-family: 'menu-font';
  src: URL('/menu.ttf') format('truetype');
}

@font-face {
  font-family: 'bold-font';
  src: URL('/bold.ttf') format('truetype');
}


@-o-keyframes bounce-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
  }
}

@-webkit-keyframes bounce-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
  }
}
@-moz-keyframes bounce-in {
  0% {
    opacity: 0;
    -moz-transform: scale(0.5);
  }

  100% {
    -moz-transform: scale(1);
  }
}
@keyframes bounce-in {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.5);
    -moz-transform: scale(0.5);
    -ms-transform: scale(0.5);
    -o-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
}

:root{
  --fontsize-p:1.2vw;
  --fontsize-nav:1.1vw;
  --fontsize-navhome:1.4vw;
  --fontsize-text:1.2vw;
  --fontsize-title:1.4vw;
  --fontsize-subtitle:1.3vw;
  --fontsize-slide_title:2.5vw;
  --fontsize-slide_subtitle:1.4vw;
  --fontsize-slide_arrow:2.3vw;
  --typing-font:3vw;
  --theme-color:#dddfdf; 
  --primary-color:#7780E4;
  --secondary-color:#0000FF;
  --text-color:#444444;
  --card-width:30vw;
}

*
{
  margin:0;
  padding: 0;
}
body {
  font-family: 'regular-font';
  background-color: var(--theme-color);
}

div {
  color: var(--text-color);
}
p {
  font-size: var(--fontsize-text);
}
.timeline {
  max-width: 100% !important;
}

a {
  text-decoration: underline;
  font-weight: bolder;
  color:var(--text-color);
}

.entry .body {
  margin: 0 0 3em;
  float: right;
  width: 80% !important;
  color: #333;
}

.timeline:before {
  border: none !important;
  border-left: 0.5px solid var(--text-color) !important;
  left: 20% !important;
}

.timeline-item-date {
  background: var(--text-color) !important;
  width: 53% !important;
  left: 1vw !important;
}
.timeline-item-dateinner {
  color: var(--text-color) !important;
  font-weight: normal !important;
  font-size: var(--fontsize-text) !important;
}

.timeline-card {
  padding-top: 1vw;
  padding-bottom: 1vw;
  border-top: 1px solid var(--text-color);
  border-bottom: 1px solid var(--text-color);
}

.timeline-mytitle {
  font-size: var(--fontsize-subtitle) !important;
}

.timeline-mysubtitle {
  font-size: var(--fontsize-subtitle)  !important;
}

.entry .title:before {
  width: 6px !important;
  height: 6px !important;
  border: 2px solid var(--text-color) !important;
  background-color: var(--theme-color) !important;
  left: 58% !important
}

.App
{
  margin-top: 1vw;
  margin-left: 7.5vw;
  margin-right: 7.5vw;
  margin-bottom: 1vw;
  width: 85vw;
  border: 1px solid var(--text-color);
  background-color: var(--theme-color);
}

.in-text-img
{
  height:1em;
}

.condiv
{
  display: block;
  width: auto;
}

*:focus {
    outline: none;
}

.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

}
.profilepic
{
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}

.navmargin{
  position: fixed;
  height:6vw;
  background:var(--theme-color);
  width: 100vw;
  left:0vw;
  top:0vw;
  z-index: 100000;
}
.mynav {
  position: absolute;
  top: 1vw;
  list-style-type: none;
  text-align: center;
  background-color: var(--theme-color);
  border-bottom: 1px solid var(--text-color);
  height: 5vw;
  padding-left: 2.5vw;
  padding-right: 2.5vw;
  overflow: hidden;
  left: 7.5vw;
  width: 80vw;
  border-top:  1px solid var(--text-color);
  border-left:  1px solid var(--text-color);
  border-right:  1px solid var(--text-color);
}

.navitem:hover{
  border-bottom: 2px solid var(--text-color);
}

.navitem {
  font-size:var(--fontsize-nav) ;
  line-height: 4.9vw;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
  display: inline-block;
  float: right;
  margin-left: 3vw;
  font-family: 'menu-font';
}
.navitemlink {
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
}
.navhome {
  font-size:var(--fontsize-navhome);
  font-family: 'bold-font';
  color: var(--text-color);
  line-height: 4.9vw;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  display: inline-block;
  float: left;
  margin: 0;
}

.context-s li {
    list-style-type: none;
    position: relative;
}


.conedu {
    padding-bottom: 1rem;
    padding-top: 1rem;
    margin-right: 2rem;
}

.conedu_title {
  font-size: var(--fontsize-subtitle);
  line-height: var(--fontsize-subtitle);
  height: var(--fontsize-title);
  margin-top: 0px;
  margin-bottom: 0px;
  padding-bottom: 0px;
  padding-top: 0px;
}

.eduinfo {
  justify-content: space-between;
  display: block;
  width: 100%;
}
.eduinfo div{
  display: inline-block;
  font-size: var(--fontsize-text);
  line-height: var(--fontsize-text);
}

.course_list_title{
  width:100%;
  font-size:var(--fontsize-text);
  height: var(--fontsize-text);
  line-height: var(--fontsize-text);
  margin: 0px;
  padding: 0px;
}

.course_list{
  width:100%;
  display: block;
}

.course_list li {
  display: inline-block;
  height: calc(1.5*var(--fontsize-text));
  line-height: calc(1.5*var(--fontsize-text));
  text-align: center;
  margin-right: 2vw;
  font-size: var(--fontsize-text)
}

.skill_list {
  columns: 5;
  -webkit-columns: 5;
  -moz-columns: 5;
  margin-bottom: 2vw;
  border-right: 1px solid var(--text-color);
  margin-right: 2rem;
}


.skill_list li {
  width: auto;
  height: 5vw;
  line-height: 5vw;
  text-align: center;
  border-left: 1px solid var(--text-color);
}

.skill_list li:hover {
  -webkit-box-shadow:inset 0px 0px 0px 1px var(--text-color);
  -moz-box-shadow:inset 0px 0px 0px 1px var(--text-color);
  box-shadow:inset 0px 0px 0px 1px var(--text-color);
}

.slideshow_container{
  width: 85vw;
  height: 42.5vw;
  overflow: hidden;
}

.slideshow_item{
  width: 85vw;
  height: 42.5vw;
  text-align: center;
  overflow: hidden;
}

.slideshow_left{
  width: 42.3vw;
  height: 42.5vw;
  display: inline-block;
  margin-top: 0px;
  padding-top: 0px;
  overflow: hidden;
  border-right: 1px solid var(--text-color);
}

.slideshow_text{
  width: 42.5vw;
  height: 25vw;
  display: inline-block;
  margin-top: 0px;
  padding-top: 12.5vw;
  vertical-align:top;
  overflow: hidden;
}
.slideshow_text h4{
  text-align: center;
  height: 5.5vw;
  line-height: 5.5vw;
  font-size:var(--fontsize-slide_title);
  font-family: 'menu-font'
}
.slideshow_text h5{
  text-align: center;
  height: 4vw;
  line-height: 4vw;
  font-size:var(--fontsize-slide_subtitle);
  font-family: 'menu-font'
}

.slideshow_text_empty{
  height: 9vw;
}

.slideshow_text a{
  text-decoration: none;
  color:var(--text-color);
}
.slideshow_text a:hover{
  text-decoration: underline;
  color:var(--text-color);
}

.slideshow_right{
  width: 42.5vw;
  height: 42.5vw;
  display: inline-block;
  overflow: hidden;
}

.slideshow_image{
  width: 42.5vw;
  height: 42.5vw;
  background-repeat: no-repeat;
  background-size: 42.5vw 42.5vw;
  display: inline-block;
  overflow: hidden;
  filter: sepia(80%) grayscale(100%) brightness(35%);
}

.slideshow_control{
  width: 42.5vw;
  height: 5vw;
  overflow: hidden;
  border-top: 1px solid var(--text-color);
  text-align: left;
  display: inline-block;
}

.slideshow_control_icon {
  font-size:var(--fontsize-nav) ;
  line-height: 4.8vw;
  height: 4.8vw;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
  display: inline-block;
  float: left;
  padding-left: 2vw;
  padding-right: 2vw;
  font-family: 'menu-font';
  border-top: 2px solid var(--theme-color);



}

.slideshow_control_sep {
  border-left:1px solid var(--text-color);
  float:right;
  height:12vw;
  margin-right:0vw
}

.slideshow_control_icon:hover {
  border-top: 2px solid var(--text-color);
}

.slideshow_control_button {
  font-size:var(--fontsize-slide_arrow) ;
  line-height: 4.2vw;
  height: 4.8vw;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
  display: inline-block;
  float: right;
  padding-left: 2vw;
  padding-right: 2vw;
  font-family: 'menu-font';
