}
.conparent h4 {
  font-size:var(--fontsize-title);
  padding-left:1rem;
  padding-right:1rem;
  margin:0px;
  line-height: 5vw;
  text-align: center;


}

.conparent h5 {
  font-size:var(--fontsize-subtitle);
  margin:0px;
  line-height: 5vw;
  overflow: hidden;
}

.consec {
