}

.consec p {
  font-size: var(--fontsize-text);
  padding-left: 1rem;
  padding-right: 1rem;
  line-height: var(--fontsize-text);
}

.consec h4 {
  font-size:var(--fontsize-title);
  padding-left:1rem;
  padding-right:1rem;
  margin:0px;
  line-height: 5vw;
  height: 5vw;
  text-align: center;
  font-family: 'menu-font'
}

.consec h5 {
  font-size:var(--fontsize-subtitle);
  margin:0px;
  line-height: 5vw;
  height: 5vw;
  padding: 0px;
}

.consec h6{
