}

.consec ol {
  font-size:var(--fontsize-text);
  padding-right: 1.5vw;
  padding-left: 1.5vw;
}

.inlinea {
  text-decoration: none;
  font-weight: bolder;
  color:var(--text-color);
}
.inlinea:hover {
  text-decoration: underline;
}

.papera {
  text-decoration: underline;
  font-weight: bolder;
  color:var(--text-color);
}

.timeline-list {
    list-style-type: none;
    position: relative;
    padding-left: 2rem;
}

.timeline-list li::before {
    content: '\2022';
    position: absolute;
    left: 1rem;
    font-size: 1.1em;
}

.context-s
{
  width: 53vw;
  height: 28vw;
  display: inline-block;
  float: right;
}

.context-sr
{
  width: 60vw;
  height: auto;
  display: inline-block;
  float: left;
}


.conimg-s
{
  width: 42.5vw;
  height: 42.5vw;
  display: inline-block;
  float: left;
  margin-right: 2vw;
  filter: sepia(80%) grayscale(100%);
}

.conimg-sr
{
  width: 17vw;
  height: 100%;
  display: inline-block;
  float: right;
  margin-left: 1vw;
}

.contactlink {
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
}
.contactlink:hover {
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
}
.socialdiv {
  width: 85vw;
  height: 5vw;
  border-top: 1px solid var(--text-color);
  overflow: hidden;
}
.social_icon {
  font-size:var(--fontsize-nav) ;
  line-height: 5vw;
  height: 5vw;
  width: 24.2vw;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: var(--text-color);
  display: inline-block;
  float: left;
  padding-left: 2vw;
  padding-right: 2vw;
  font-family: 'menu-font';
  border-top: 2px solid var(--theme-color);
  margin-left: auto;
  margin-right: auto;
}
.social_icon:hover {
  border-top: 2px solid var(--text-color);
}

.big_button {
  text-decoration: underline;
  color: var(--text-color);
  font-size: var(--fontsize-slide_title);
  font-family: 'bold-font';
}
.big_button:hover {

}
.card_container {
  -moz-column-count:2;
