}
.card {
  width: var(--card-width);
  height: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2vw;
  margin-bottom: 2vw;
  break-inside: avoid;
  overflow: hidden;
}

.card_a{
  text-decoration: none;
  color: var(--text-color);
}
.card_title{
  font-family: 'menu-font';
  width: var(--card-width)  !important;
  text-align: left ;
  font-size: var(--fontsize-subtitle) !important;
  line-height:  var(--fontsize-subtitle) !important;
  height: var(--fontsize-subtitle) !important;
