}
.card_subtitle{
  font-family: 'regular-font';
  width: var(--card-width) !important;
  text-align: left ;
  font-size: var(--fontsize-text) !important;
  line-height:  var(--fontsize-text) !important;
  height: var(--fontsize-subtitle) !important;
  margin-bottom: var(--fontsize-text) !important;
}
.card_desc{
  font-family: 'regular-font';
  width: var(--card-width) !important;
  text-align: left ;
  font-size: var(--fontsize-text) !important;
  line-height:  var(--fontsize-text) !important;
  height: auto;
  overflow: hidden;
  padding-bottom: var(--fontsize-text) !important;
  border-bottom: 1px solid var(--text-color);
}
.card img{
  width: var(--card-width);
  height: auto;
