    position: absolute;
    top:0px;
    left:0px;
    background-repeat: no-repeat;
    background-size: 100% auto;
    z-index: -100;
    filter: blur(6px) sepia(80%) grayscale(100) brightness(35%);
  }
  .navmargin{
    position: fixed;
    height:7vw;
    background:var(--theme-color);
    width: 100vw;
    left:0vw;
    top:0vw;
    z-index: 100000;
  }
  .mynav {
    padding-left: 0vw;
    padding-right: 0vw;
    height: 8vw;
    position: absolute;
    top: 2vw;
    left: 2vw;
    right: 2vw;
    border-top:  1px solid var(--text-color);
    border-left:  1px solid var(--text-color);
    border-right:  1px solid var(--text-color);
  }
  .navitem {
    font-size:var(--fontsize-nav) ;
    display: block;
    margin-left: 0vw;
    margin-right: 0vw;
    width: 40vw;
    font-family: 'menu-font';
  }
  .navitem:hover {
    border-bottom: none;
  }
  .App
  {
    border-top: none;
    margin-top: 2vw;
    margin-bottom: 2vw;
    margin-left: 2vw;
    margin-right: 2vw;
  }

  .entry .title:before {
    left: 2.2% !important;
  }

  .timeline:before {
    left: calc(1% + 6px) !important;
  }
  .entry .title {
    float: left !important;
    width: 70%   !important;
  }
  .timeline-item-date {
    margin-left: 30px !important;
  }
  .entry .body {
    margin: 20px 0 3em !important;
    float: right !important;
    width: 99% !important;
  }
  .timeline-item-date {
    background: var(--text-color) !important;
    width: 100% !important;
    left: 1vw !important;
  }
  .consec ul {
    list-style-type: none;
    text-decoration: none;
    font-size:var(--fontsize-text);
  }
  .consec h4 {
    font-size:var(--fontsize-title);
    padding-left:1rem;
    padding-right:1rem;
    margin:0px;
    line-height: 7vw;
    height: 7vw;
  }

  .consec h5 {
    font-size:var(--fontsize-subtitle);
    line-height: var(--fontsize-subtitle);
    height: 5vw;
    margin-bottom: 5vw;
    width: 100%;
  }
  .conparent h4 {
    font-size:var(--fontsize-title);
    padding-left:1rem;
    padding-right:1rem;
    margin:0px;
    line-height: 7vw;
  }

  .conparent h5 {
    font-size:var(--fontsize-subtitle);
    margin:0px;
    line-height: 7vw;
  }
  .skill_list {
    columns: 3;
    -webkit-columns: 3;
    -moz-columns: 3;
  }

  .skill_list li:hover {
    -webkit-box-shadow:none;
    -moz-box-shadow:none;
    box-shadow:none;
  }
  .card_container {
    -moz-column-count:1;
    -webkit-column-count:1;
    column-count:1;
  }
  .card_title{
    margin-bottom: var(--fontsize-text) !important;
  }
  .card_subtitle{
    font-family: 'regular-font';
    width: var(--card-width) !important;
    text-align: left ;
    font-size: var(--fontsize-text) !important;
    line-height:  var(--fontsize-text) !important;
    height: var(--fontsize-subtitle) !important;
    margin-bottom: var(--fontsize-text) !important;
  }
  .card_desc{
    margin-top: var(--fontsize-text) !important;
  }
  .consec h6{
    font-size:var(--fontsize-subtitle);
    margin-top:3vw;
    margin-bottom: 1.5vw;
    line-height: 2vw;
    height: var(--fontsize-subtitle);
    padding: 0px;
  }
  .slideshow_control_button{
    line-height: 4vw;
    height: 6vw;
  }
  .slideshow_control_icon{
    line-height: 6vw;
    height: 6vw;
  }
  .slideshow_control_sep{
    height: 16vw;
    margin-bottom: -3vw;
  }
  .slideshow_indicator{
    height: 7vw;
    width: 7vw;
    left: 0;
    right: auto;
    line-height: 7vw;
  }
  .socialdiv {
    width: 96vw;
    height: 7vw;
  }
  .social_icon{
    height: 7vw;
    line-height: 7vw;
    width: 22vw;
  }
}


@media all and (max-width: 767px) {
  :root {
    --fontsize-p:0.8rem;
    --fontsize-nav:0.7rem;
    --fontsize-navhome:0.7rem;
    --fontsize-text:0.71rem;
    --fontsize-title:0.75rem;
    --fontsize-subtitle:0.72rem;
    --fontsize-slide_title:0.9rem;
    --fontsize-slide_subtitle:0.8em;
    --fontsize-slide_arrow:1.0rem;
    --typing-font:3rem;
  }

  .slideshow_indicator{
    height: 9vw;
    width: 9vw;
    left: 0;
    right: auto;
    line-height: 9vw;
  }
  .slideshow_control{
    width: 96vw;
    height: 8vw;
    margin-left: -0.5vw;
  }
  .slideshow_container
  {
    width: 96vw;
    height: 67.3vw;
    overflow: hidden;
  }
  .slideshow_left
  {
    width: 100%;
    height: 67.3vw;
  }
  .slideshow_control
  {
    height: 9vw;
    width: 95.3vw;
    background: var(--theme-color);
    margin-top: -0.5vw;
    overflow: hidden;
  }
  .slideshow_control_icon {
    font-size:var(--fontsize-nav) ;
    line-height: 7vw;
    height: 7vw;
    text-align: center;
    list-style-type: none;
    text-decoration: none;
    color: var(--text-color);
    display: inline-block;
    float: left;
    padding-left: 2vw;
    padding-right: 2vw;
    font-family: 'menu-font';
    border-top: 2px solid var(--theme-color);
  }


  .slideshow_control_icon:hover {
    border-top: 2px solid var(--text-color);
  }

  .slideshow_control_button {
    font-size:var(--fontsize-slide_arrow) ;
    line-height: 7vw;
    height: 7vw;
    text-align: center;
    list-style-type: none;
    text-decoration: none;
    color: var(--text-color);
    display: inline-block;
    float: right;
    padding-left: 2vw;
    padding-right: 2vw;
    font-family: 'menu-font';
    border-top: 2px solid var(--theme-color);
    cursor: pointer;
  }


  .slideshow_text{
    margin-top: 10vw;
    width: 100%;
    height: 36vw;
  }
  .slideshow_item{
    width: 100%;
    height: 67.3vw;
    margin-top: 0vw;
  }
  .slideshow_control_sep {
    border-left:1px solid var(--text-color);
    float:right;
    height:12vw;
    margin-right:0vw;
    margin-bottom: -2vw;
  }

  .socialdiv {
    width: 96vw;
    height: 7vw;
    border-top: 1px solid var(--text-color);
    overflow: hidden;
  }
  .social_icon {
    font-size:var(--fontsize-nav) ;
    line-height: 7vw;
    height: 7vw;
    width: 27vw;
  }
  .slideshow_text h5{
    text-align: center;
    height: 12vw;
    line-height: 4vw;
    font-size:var(--fontsize-slide_subtitle);
  }
  .consec h4 {
    font-size:var(--fontsize-title);
    padding-left:1rem;
    padding-right:1rem;
    margin:0px;
    line-height: 10vw;
    height: 10vw;
    text-align: center;
  }

  .consec h5 {
    font-size:var(--fontsize-subtitle);
    margin:0px;
    line-height: 10vw;
    height: 10vw;
  }
  .conparent h4 {
    font-size:var(--fontsize-title);
    padding-left:1rem;
    padding-right:1rem;
    margin:0px;
    line-height: 7vw;
    height: 6vw;
    text-align: center;
  }

  .conparent h5 {
    font-size:var(--fontsize-subtitle);
    margin:0px;
    line-height: 5.3vw;
    height: 5.3vw;
  }
  .navmargin{
    position: fixed;
    height:10vw;
    background:var(--theme-color);
    width: 100vw;
    left:0vw;
    top:0vw;
    z-index: 100000;
  }
  .mynav {
    height: 8vw;
  }
  .skill_list {
    columns: 2;
    -webkit-columns: 2;
    -moz-columns: 2;
  }

  .timeline-mytitle {
    line-height: var(--fontsize-subtitle) !important;
  }

  .timeline-item-dateinner{
    height: 32px !important;
    line-height: 32px !important;
  }
  .timeline-item-date {
    height: 34px !important;
    top: 0px;
  }
  .entry .body {
    margin: 0 0 2em!important;
  }

}
